/* Hover Overlay */

.block-display-img {
  display: block;
}

.overlay-container {
  position: relative;
  &:hover {
    .overlay {
      background-color: rgba(255, 114, 0, 0.7);
    }
    .overlay-title,
    .overlay-title-advisors {
      opacity: 1;
    }
  }
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  transition: .35s ease;
  background-color: rgba(255, 114, 0, 0);
}

.overlay-title {
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  opacity: 0;
  transition: .35s ease;
}

.overlay-title-advisors {
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translate(-0%, -50%);
  -ms-transform: translate(-0%, -50%);
  opacity: 0;
  transition: .35s ease;
}

.bio-reveal {
  width: 550px;
